$backgroundColor:#fab411;
$sunInnerColor:rgb(250, 240, 182);
$sunOuterColor:rgb(253, 228, 148);
$sun_chart_width_small: 264px;
$sun_chart_width_large: 230px;
$bar_precent_bg:rgb(246, 230, 142);
$bar_bg:#f1a10b;
$textShadow: 0 0 2px rgba(87, 87, 87, 0.196);

@mixin sunImg {
  border-radius: 50%;
  background-color: $sunInnerColor;
  -webkit-box-shadow: 0px -2px 8px 2px $sunOuterColor; 
  box-shadow: 0px -2px 8px 2px  $sunOuterColor;
}
.sun{
  margin-top: 32px;
  .inner{
    background-color: $backgroundColor;
    padding:15px 24px 30px;
    position: relative;
  }
  .title{
    color:#ffff;
    text-shadow: 0 0 2px rgba(0, 0, 0, 0.196);
    font-size: 17px;
    line-height: 23px;
    margin: 0;
    font-weight: bold;
    padding-bottom: 36px;
  }
  .btn_wrap{
    position: absolute;
    top:15px;
    right: 20px;
  }
  .btn_sun{
    width: 42px;
    height: 32px;
    background-color:#ffff;
    border:none ;
    &:first-child{
      border-top-left-radius: 5px;
      border-bottom-left-radius: 5px;
    }
    &:last-child{
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    &.on{
      background-color: #ed8b02;
      color:#fff;
      font-weight: 600;
      text-shadow: 0 0 2px rgba(90, 90, 90, 0.196);
    }
  }

  span{
    display: inline-block;
  }
  .icon_area{
    position: relative;
    width: 6rem;
    height:calc(6rem * 0.5);
    overflow: hidden;
  }
  .sunIcon_arrow{
    display: block;
    position: absolute;
    top:0;
    left:calc( (100% - 1rem) /2);
    font-size: 1rem;
    width: 1rem;
    height: 1rem;
    color: rgb(250, 240, 182) ;
    .up{
      animation: moveArrow 2s ease-in infinite  ;
    }
    .down{
      animation: moveArrow 2s ease-out infinite reverse;
    }
    
  }
  @keyframes moveArrow {
    from{
      transform: translateY(5px);
    }
    to{
      transform: translateY(0px);
    }
  }

  .sunIcon{
    width: calc(6rem * 0.5);
    height: calc(6rem * 0.5);
    position: absolute;
    top:calc(6rem * 0.25);
    left:calc(6rem * 0.25);
    @include sunImg;
  }
  //today
  .sun_chart  , .current_bar{
    width: $sun_chart_width_small;
  }
  
  .move_sun, .current_bar .bar{
    width: $sun_chart_width_small;
    height: $sun_chart_width_small;
  }
  .sun_chart{
    position: relative;
    height: 136px;
    margin: 0 auto;
    transform-origin: 50% 100% ;
  }
  
  .move_sun{
    position: absolute;
    z-index: 10;
    &::after{
      width: 36px;
      height: 36px;
      content: '';
      position: absolute;
      top:51%;
      left: -15px;
      @include sunImg;
    }
  }
  .progress_bar{
    width: inherit;
    height:inherit;
    &::before , &::after{
      position: absolute;
      bottom: 0px;
      width: 7px;
      height: 7px;
      border-radius: 50%;
      content: '';
      z-index: 5;
    }
    &::before{
      left: 0;
      background-color: $bar_precent_bg;
    }
    &::after{
      background-color: $bar_bg;
      right: 0;
    }
  }
  .current_bar{
    height: inherit;
    position: absolute;
    overflow: hidden;
    .bar{
      position: absolute;
      top:0;
      left: 0;
      border-radius: 50%;
      border:7px solid $bar_bg;
      border-bottom: 7px solid $bar_precent_bg;
      border-right: 7px solid $bar_precent_bg;
      box-sizing: border-box;
    }
  }
  .sun_info{
    position: absolute;
    bottom:0;
    left: 16px;
    width: calc(100% - 14px - 18px);
    height: 70%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    .icon_area{
      display: block;
    }
  }
  .time_list{
    display: inline-block;
    line-height: 24px;
    margin-top: 10px;
    color:#ffff;
    text-shadow:$textShadow;
  }
  .time_title, .time_sun{
    display: inline-block;
  }
  .time_title {
    font-size: 1rem;
    margin-right: 5px;
    &.set{
      margin-left: 18px;
    }
  }
  .time_sun{
    font-size: 18px;
    font-weight: 600;
  }
  // tomorrow 
  .sun_table, thead, tbody, tr{
    width: 100%;
  }
  .sun_table{
    color:#ffff;
    font-size: 14px;
    font-weight: 500;
    text-shadow:$textShadow;
    tr{
      position: relative;
    }
    th, td{
      width: 33%;
    }
    td{
      text-align: center;
    }
    thead .blind{
      display: inline-block;
      position: absolute;
      top: -100%;
      left: -100%;
    }
    .sun_tr{
      line-height: 20px;
      position: relative;
      &::after{
        width: 100%;
        height: 0.5px;
        display: inline-block;
        background-color: rgba(255, 255, 255, 0.61);
        content: '';
        position: absolute;
        bottom:0;
        left:0
      }
    }
    .sun_th, .sun_tr td {
      padding: 10px 0 17px 0;
    }
    .sun_th{
      font-weight: initial;
    }
    .sun_day , strong{
      margin-left: 5px;
    }
  }
}
@media screen and (max-width:375px) {

  .sun{
    .inner{
      padding-left: 10px;
      padding-right: 18px;
    }
    .btn_wrap{
      right: 30px;
    }
    .sun_chart,  .current_bar{
      width:240px;
    }
    .sun_chart{
      height: 123px;
    }
    .move_sun, .current_bar .bar{
      width:240px;
      height:240px;
    }
    .move_sun::after{
      width: 24px;
      height: 24px;
      left:-8px;
    }
  }
}

@media screen and (min-width:1025px) {
  .sun{
    margin-top:  40px ;
    .inner{
      border-radius: 10px;
      padding:15px 5%;
      box-sizing: border-box;
    }
    .sun_chart , .current_bar{
      width: $sun_chart_width_large;
    }
    .sun_chart {
      height: 115px;
    }
    .move_sun, .current_bar .bar{
      width: $sun_chart_width_large;
      height: $sun_chart_width_large;
    }
    .move_sun::after{
      width: 30px;
      height: 30px;
      left:-12px;
    }
  }
}
